@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

*, *::before, *::after {
    box-sizing: border-box;
}

html, body {
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 10px;
}

button {
    outline: none;
    border: none;
    cursor: pointer;
    background: none;
}

.alert-default {background-color: transparent;}
.alert-info {background-color: rgba($color: #000000, $alpha: 0.2);}